.footer {
    margin: 100px 200px 20px 200px;
  
    .side1 {
      display: flex;
      gap: 50px;
  
      .item {
        flex: 1;
        display: flex;
        flex-direction: column;
        gap: 10px;
        text-align: justify;
        font-size: 14px;
  
        h1{
          font-size: 18px;
          font-weight: 500;
          color: #555;
        }
  
        span{
          color: gray;
        }
      }
    }
  
    .side2 {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: 50px;
      .left {
        display: flex;
        align-items: center;
  
        .logo{
          color: black;
          font-weight: bold;
          font-size: 24px;
        }
  
        .copyright{
          margin-left: 20px;
          font-size: 12px;
          color: gray;
        }
      }
      .right {
  
        img{
          height: 50px;
        }
      }
    }  

  @media (max-width: 768px) {
      margin: 50px 100px 20px 100px;

    .side1 {
      flex-wrap: wrap;
      gap: 20px;
    }

    .side2 {
      flex-direction: column;
      align-items: flex-start;
      margin-top: 30px;

      .left {
        margin-bottom: 10px;
      }
    }
  }

  @media (max-width: 480px) {
    margin: 50px 50px 20px 50px;

    .side1 {
      flex-wrap: wrap;
      gap: 20px;
    }

    .side2 {
      flex-direction: column;
      align-items: center;
      margin-top: 30px;
    }
  }  
}